        <span></span>
        <span></span>
      </div>
    </button>
    <div class="sidebar" :class="[isSidebarActive  ? 'sidebar-slide-in' : 'sidebar-slide-out']">
      <router-link to="/" class="logo">
        <img alt="MoleQ Logo" src="../assets/logo.png" />
        <p>MoleQ Inc</p>
      </router-link>
      <ul class="sidebar-menu">
        <li v-for="menu in menuBar" :key="menu">
          <a class="menu-btn dropbtn" :class="menu.show ? 'collasped' : ''"  @click="menu.show = !menu.show">
            <span><fa :icon="menu.icon"></fa> {{ $t("nav." + menu.name) }}</span>
            <span><fa icon="angle-down" /></span>
          </a>
          <Transition v-on:enter="enter" v-on:leave="leave">
            <ul class="sidebar-sub-menu" v-if="menu.show">
              <!-- <li class="dropbtn" v-for="subMenu in menu.subMenu" :key="subMenu">
                <div v-if="!subMenu.subSubMenu">
                  <router-link :to="subMenu.url">{{ subMenu.name }} <fa icon="angle-right" /></router-link>
                </div>
                <div v-else>
                  <a :class="subMenu.show ? 'collasped' : ''" @click="subMenu.show = !subMenu.show">
                    {{ subMenu.name }}
                    <span><fa icon="angle-down" /></span>
                  </a>
                </div>
                <Transition v-on:enter="enter" v-on:leave="leave"> -->
                  <ul class="sidebar-sub-sub-menu" v-if="menu.show">
                    <li class="dropbtn" v-for="subMenu in menu.subMenu" :key="subMenu">
                      <router-link :to="subMenu.url" @click="goPage()">
                        {{ $t("nav." + subMenu.name) }}
                      </router-link>
                    </li>
                  </ul>
                <!-- </Transition>
              </li> -->
            </ul>
          </Transition>
        </li>
        <a class="menu-btn logout-btn" @click="logUserOut()"><span><fa icon="sign-out" />  {{ $t("nav.sign_out") }}</span></a>
      </ul>
    </div>
  </div>
</div>
</template>

