          show: true,
          icon: "bar-chart",
          subMenu: [
            // {
              // name: "POS",
              // show: true,
              // subSubMenu: [
                { name: "store_sales", url: "/pos-reports/store-sale" },
                { name: "transactions", url: "/pos-reports/transaction" },
                { name: "station_summary", url: "/pos-reports/station-summary" },
                { name: "department", url: "/pos-reports/department" },
                { name: "item_sales", url: "/pos-reports/item-sale" },
              // ],
            // },
            // { name: "Inventory", url: "/inventory" },
          ],
        },
        // {
        //   id: 2,
        //   name: "Inventory",
        //   show: false,
        //   icon: "sitemap",
        //   subMenu: [],
        // },
        // {
