      ],
    };
  },
  mounted() {},
  beforeMount() {
    window.addEventListener('click', this.close);
  },
  unmounted() {
    window.removeEventListener('click', this.close);
  },
  methods: {
    enter(el, done) {
      Velocity(el, "slideDown", { duration: 250, complete: done });
    },
    leave(el, done) {
      Velocity(el, "slideUp", { duration: 250, complete: done });
    },
    toggleSidebar() {
      this.isSidebarActive = !this.isSidebarActive;
    },
    goPage() {
      this.isSidebarActive = false;
    },
    close(e) {
      if (!this.$refs.toggleSidebar.contains(e.target)) {
        this.isSidebarActive = false;
      }
    },
    logUserOut() {
      localStorage.removeItem("LoginAccessToken");
      this.auth.logout();
    },
  },
};
