<style scoped>
.overlay-black{display: none;}
.sidebar {
  font-weight: 350;
  height: 100%;
  width: 220px;
  position: fixed;
  top: 0;
  left: 0;
  background-image: linear-gradient(to bottom, #071a54, #122865, #1b3676, #244587, #2d5598);
  color: #ffff;
  overflow-x: hidden;
  padding-top: 5px;
  /* border-right: 1px solid #ddd; */
  /* box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2); */
  /* border-radius: 2px; */
  z-index: 39;
}
.logo {
  display: flex;
  color: #ffff;
  padding: 6px 8px;
  text-align: center;
  text-decoration: none;
  margin-bottom: 10px;
  align-items: center;
  border-bottom: 1px solid #ffffff8c;
}
.logo img {
  width: 45px;
  margin: 0 10px;
}
.sidebar-menu .menu-btn {
  display: flex;
  gap: 10px;
  font-size: 15px;
  margin: 0 10px;
  padding: 5px 10px;
  border-radius: 5px;
  justify-content: space-between;
}
.sidebar-menu li {
  padding: 5px 0px;
}
.sidebar-menu a {
  text-decoration: none;
  color: #ffffff;
}
.sidebar-sub-menu {
  padding-left: 35px;
}
/* .sidebar-sub-sub-menu {
  padding-left: 15px;
} */
.sidebar-sub-menu .dropbtn li:last-child {
  padding-bottom: 0px;
}
.sidebar-sub-menu li {
  width: 100%;
  display: table;
  cursor: pointer;
}
.sidebar-sub-menu a {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  border-radius: 5px;
  margin-right: 10px;
  padding: 5px 10px;
}
.sidebar-menu .dropbtn .menu-btn:hover,
.dropbtn a:hover,
.logout-btn:hover  {
  background-color: #ffffff8a;
  color: #000 !important;
  transition: 0.3s ease;
}
.sidebar-menu .dropbtn .router-link-active:hover,
.sidebar-menu .dropbtn .router-link-active {
  background-color: #fff !important;
  color: #000 !important;
  transition: 0.3s ease;
}
.svg-inline--fa {
  width: 1.5em;
}
.fa-angle-down{
  transform: rotate(180deg);
  transition: transform .25s;
}
.collasped .fa-angle-down{
  transform: rotate(0);
}
.sidebar-toggle {
  display: none;
  position: absolute;
  top: 10px;
  right: 10px;
  /* right: 55px; */
  /* background-color: #fff; */
  background-color: transparent;
  color: #000;
  border: none;
  border-radius: 25px;
  padding: 6px;
  z-index: 10;
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
}
.sidebar-toggle:hover {
  background: rgba(0, 0, 0, 0.07);
}
#nav {
  width: 20px;
  height: 20px;
  /* position: relative;
  margin: 50px auto; */
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);

  cursor: pointer;
}
#nav span {
  display: block;
  position: absolute;
  height: 3px;
  width: 20px;
  background: #000;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}
#nav span:nth-child(1) {
  top: 2px;
}
#nav span:nth-child(2),#nav span:nth-child(3) {
  top: 8px;
}
#nav span:nth-child(4) {
  top: 14px;
}
#nav.open span:nth-child(1) {
  top: 8px;
  width: 0%;
  left: 50%;
}
#nav.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}
#nav.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
#nav.open span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
}
.logout-btn {
  position: absolute;
  width: calc(100% - 20px);
  /* display: inline-block; */
  bottom: 15px;
}
@media only screen and (max-width: 1550px) {
  .sidebar-toggle{
    display: block;
  }
  .sidebar {
    transform: translateX(-100%);
    -webkit-transform: translateX(-100%);
  }
  .sidebar-slide-in {
    animation: sidebar-slide-in 0.5s forwards;
    -webkit-animation: sidebar-slide-in 0.5s forwards;
  }
  .sidebar-slide-out {
    animation: sidebar-slide-out 0.5s forwards;
    -webkit-animation: sidebar-slide-out 0.5s forwards;
  }
  @keyframes sidebar-slide-in {
    100% { transform: translateX(0%); }
  }

  @-webkit-keyframes sidebar-slide-in {
    100% { -webkit-transform: translateX(0%); }
  }
      
  @keyframes sidebar-slide-out {
    0% { transform: translateX(0%); }
    100% { transform: translateX(-100%); }
  }

  @-webkit-keyframes sidebar-slide-out {
    0% { -webkit-transform: translateX(0%); }
    100% { -webkit-transform: translateX(-100%); }
  }
}
@media only screen and (max-width: 500px) {
  .sidebar-sub-menu a,
  .sidebar-menu .menu-btn span,
  .sidebar-toggle {
    font-size: 3.8vw;
  }
  .sidebar-menu .menu-btn,
  .sidebar-sub-menu a {
    padding: 10px;
  }
  .logo {
    border-bottom: 1px solid #ddd;
  }
  .logo p{
    color: #000;
    font-size: 4.5vw;
    font-weight: 600;
  }
  .sidebar {
    width: 260px;
    background-color: #fff;
    background-image: none;
    border-radius: 0;
  }
  .sidebar-menu a {
    color: #000;
  }
  .sidebar-menu .dropbtn .menu-btn:hover,
  .dropbtn a:hover {
    background-color: #ddd;
  }
  .sidebar-menu .dropbtn .router-link-active:hover,
  .sidebar-menu .dropbtn .router-link-active {
    background-color: #ddd !important;
  }
  /* .sidebar {
    width: 100%;
    height: fit-content;
    top: 50px;
    transform: scaleY(0);
    transform-origin: top;
    transition: transform 0.4s ease-in-out;
  }
  .sidebar-slide-in {
    transform: scaleY(1);
    animation: none;
  }
  .sidebar-slide-out {
    animation: none;
  }
  .sidebar-menu li{
    margin-bottom: 5px;
  } */
}
@media screen and (pointer:coarse) {
  .overlay-black{display: block;}
  .sidebar-toggle{
    right: unset;
    left: 10px;
  }
}
</style>
