/* eslint-disable */
import Oidc from "oidc-client";

var config = {
  authority: "https://moleqcloudauthenticationserverdev.azurewebsites.net",
  client_id: "ErineClient",
  // redirect_uri: window.location.origin,
  // post_logout_redirect_uri: window.location.origin,
  redirect_uri: window.location.origin + "/bkoffice-reports",
  post_logout_redirect_uri: window.location.origin + "/bkoffice-reports",
  response_type: "code",
  scope: "openid offline_access CloudUserIdentity EstherAPI",
  identity_uri: "https://moleqcloudidentityserverdev.azurewebsites.net/Identity",
  // accessTokenExpiringNotificationTime: 1,
  // automaticSilentRenew: true,
  // accessTokenExpiring: 60
};

var mgr = new Oidc.UserManager(config);

export default class MQCloudAuthService {
  login() {
    mgr.signinRedirect().catch(function (err) {
      console.log(err);
    });
  }

  logout() {
    mgr
      .signoutRedirect()
      .then(function (resp) {
        console.log("signed out", resp);
      })
      .catch(function (err) {
        console.log(err);
      });
  }
  
  currentUser() {
    let self = this;
    return new Promise((resolve, reject) => {
      mgr
        .getUser()
        .then(function (user) {
          if (user == null) {
            self.login();
            return resolve(null);
          } else {
            return resolve(user);
          }
        })
        .catch(function (err) {
          console.log(err);
          return reject(err);
        });
    });
  }

  userIdentity() {
    let self = this;
    return new Promise((resolve, reject) => {
      mgr
        .getUser()
        .then(function (user) {
          if (user == null) {
            self.login();
            return resolve(null);
          } else {
            var url = config.identity_uri + "/" + user.profile.sub;
            var xhr = new XMLHttpRequest();
            xhr.open("GET", url, false);
            xhr.onload = function () {
              if (xhr.status === 200){
                return resolve(JSON.parse(xhr.responseText));
              } else {
                return reject();
              }
            };
            xhr.setRequestHeader("Authorization", "Bearer " + user.access_token);
            xhr.send();
          }
        })
        .catch(function (err) {
          console.log(err);
          return reject(err);
        });
    });
  }

  accessToken() {
    let self = this;
    return new Promise((resolve, reject) => {
      mgr
        .getUser()
        .then(function (user) {
          if (user == null) {
            self.login();
            return resolve(null);
          } else {
            return resolve(user.access_token);
          }
        })
        .catch(function (err) {
          console.log(err);
          return reject(err);
        });
    });
  }

  apiResources() {
    let self = this;
    return new Promise((resolve, reject) => {
      mgr
        .getUser()
        .then(function (user) {
          if (user == null) {
            self.login();
            return resolve(null);
          } else {
            return resolve(user.scopes);
          }
        })
        .catch(function (err) {
          console.log(err);
          return reject(err);
        });
    });
  }

  sessionState() {
    let self = this;
    return new Promise((resolve, reject) => {
      mgr
        .getUser()
        .then(function (user) {
          if (user == null) {
            self.login();
            return resolve(null);
          } else {
            return resolve(user.session_state);
          }
        })
        .catch(function (err) {
          console.log(err);
          return reject(err);
        });
    });
  }
}
