import "./assets/css/main.css";
import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import i18n from "./i18n"
import axios from "axios";
// axios.defaults.baseURL = "http://192.168.168.104:89/Backoffice";
// axios.defaults.headers.common['Authorization'] = 'Bearer' + localStorage.getItem('LoginAccessToken');
// axios.defaults.headers.common["marketid"] = localStorage.getItem('StoreID');

// import { config } from "@vue/test-utils";
// config.global.plugins = [i18n];

// icons
import { library } from "@fortawesome/fontawesome-svg-core";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
import { fas } from "@fortawesome/free-solid-svg-icons";
import { far } from '@fortawesome/free-regular-svg-icons'
library.add(fas, far)

// datatable
import PrimeVue from "primevue/config";
import DataTable from 'primevue/datatable';
import Column from 'primevue/column';
import ColumnGroup from 'primevue/columngroup';
import Row from 'primevue/row';
import Paginator from 'primevue/paginator';
import Toast from 'primevue/toast';

// calendar
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'

//chart
import VueApexCharts from "vue3-apexcharts";

const app = createApp(App);

app.config.globalProperties.$t = i18n.global.t

app.component("fa", FontAwesomeIcon);
app.component("DataTable", DataTable);
app.component("Column", Column);
app.component("ColumnGroup", ColumnGroup);
app.component("Row", Row);
app.component("Paginator", Paginator);
app.component("Toast", Toast);
app.component('VueDatePicker', VueDatePicker);
app.component('VueApexCharts', VueApexCharts);

app.use(router, axios, PrimeVue, i18n)

app.mount("#app");
