import { createI18n } from 'vue-i18n';
import en from './lang/en/en.json';
import ch from './lang/ch/ch.json';

const i18n = createI18n({
  legacy: false,
  locale: localStorage.getItem('lang') || 'en',
  fallbackLocale: 'en',
  messages: {
    en,
    ch,
  },
});

export default i18n;
